import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			gender: ['男', '女'],
			array: ['一年级', '二年级', '三年级', '四年级', '五年级', '六年级'],
			uploadInfo: {
				nickname: '', //昵称
				avatar: '',
				gender: '', //性别
				birthday: '', //生日
				mobile: '', //手机
				grade: '', //年级
				province: '', //省份
				city: '', //城市
				area: '', //地区
				region: [],
				classes: []
			}
		}
	},
	onLoad() {

	},
	methods: {
		//性别选择点击
		bindSexChange(e) {
			this.uploadInfo.gender = e.target.value;
		},
		//生日选择点击
		bindBirthdayChange(e) {
			this.uploadInfo.birthday = e.target.value;
		},
		//年级选择点击
		bindClassChange(e) {
			this.uploadInfo.grade = e.target.value;
		},
		//地区点击出现选项
		toggleTab() {
			this.$refs.region.show();
		},
		//地区选择点击
		bindCityChange(e) {
			this.uploadInfo.region=e.checkArr;
		},
		//保存点击
		sengjibtn(){
			console.log(1);
		}
	},
	mounted() {

	}
}
